import type { PropsWithChildren } from 'react';
import type React from 'react';
import { UIWrapperContextProvider } from './ui-wrapper';

import { GlobalContext } from './global';
import { MeetingContextProvider } from './meeting';
import type { RcvEngine } from '@ringcentral/video-sdk';
import { AvatarContextProvider } from './avatar';
import {StreamProvider} from './stream';

export const RcvEngineProvider: React.FC<PropsWithChildren<{
  rcvEngine: RcvEngine | null;
  localeConfig?: Record<string, string>
}>> = ({ rcvEngine, localeConfig, children }) => {

  return (
    <GlobalContext.Provider value={{ rcvEngine, localeConfig }}>
      <UIWrapperContextProvider>
        <StreamProvider>
          <AvatarContextProvider>
            <MeetingContextProvider>
              {children}
            </MeetingContextProvider>
          </AvatarContextProvider>
        </StreamProvider>
      </UIWrapperContextProvider>
    </GlobalContext.Provider>
  );
};

export * from './global';
export * from './meeting';
export * from './ui-wrapper';
